<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 支付订单
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div style="display: flex;">
            <div>
                <el-card style="width:100%">
                    <div class="user-info">
                        <i class="el-icon-location-information user-avator"></i>
                        <div class="user-info-cont">
                            <div class="user-info-name">{{username}}</div>
                            <div>{{telephone}}</div>
                            <div>浙江理工大学科技与艺术学院</div>
                        </div>
                    </div>
                </el-card>
                <el-card class="orderInner" style="width:100%" v-for="(item,i) in goods" :key="item.orderNum">
                    <div>第{{i+1}}个订单</div>
                    <el-card v-for="(item2) in item.goods" :key="item2.goodId" style="margin-top: 10px;">
                        <div class="bussiness">
                            <i class="el-icon-s-custom"></i>
                            <span>{{item2.goodOwner}}</span>
                        </div>
                        <div class="user-info">
                            <img src="../../assets/img/img.jpg" class="user-avator" alt />
                            <div class="orderInfo">
                                <div class="infoMes">{{item2.goodName}}</div>
                                <div >
                                    <el-tag size="mini" style="margin-right: 5px;" type="danger">7天保价</el-tag>
                                    <el-tag size="mini" style="margin-right: 5px;" type="danger">平台无忧购</el-tag>
                                    <el-tag size="mini" style="margin-right: 5px;" type="danger">7天无理由退换</el-tag>
                                </div>
                            </div>
                            <div class="money">￥{{item2.goodPrice}}</div>
                        </div>
                        <div>
                            <div class="innerMsg">订单编号：{{item.orderNum}}</div>
                            <div class="innerMsg">下单时间：{{item.orderTime}}</div>
                            <div class="innerMsg">订单描述：{{item2.goodDescription}}</div>
                        </div>
                    </el-card>

                </el-card>
            </div>
            <div>
                <el-card style="width:100%;margin-left: 20px">
                    <div style="color:white;margin-right:300px;"></div>
                    <el-switch
                            v-model="value"
                            active-text="支付宝支付"
                            inactive-text="微信支付">
                    </el-switch>
                    <el-button style="margin-left: 30px;" type="success">确认支付</el-button>
                    <div style="width: 600px;">
                        <img src="../../assets/img/alipay.png" width="25%" class="user-avator" alt />
                        <img src="../../assets/img/wechat.png" width="25%" class="user-avator" alt />
                    </div>

                </el-card>
            </div>
        </div>


    </div>
</template>

<script>
    export default {
        name: "payOrder",
        data(){
            return{
                telephone:'',
                goods:[],
                value:''
            }
        },
        created() {
            this.getData()
            this.getOrder()
        },
        methods:{
            getData() {
                this.$http.get('/user/findAllUsers')
                    .then((data) => {
                        console.log(data.data);
                        data.data.forEach(item=>{
                            if(item.userName == localStorage.getItem('ms_username')){
                                this.telephone = item.phoneNum
                            }
                        })

                    })
                    .catch((err) => {
                        console.log(err);
                    })
            },
            getOrder(){
                let userName = localStorage.getItem('ms_username')
                this.$http.get('/orderList/findOrdersByUserName?userName='+userName)
                .then((data)=>{
                    console.log(data);
                    if(data.status == '200'){
                      this.goods = data.data
                   }
                })
                .catch(err=>console.log(err))
            }
        },
        computed:{
            username(){
                return localStorage.getItem('ms_username')
            }
        }
    }
</script>

<style scoped>

    .bussiness{
        margin-bottom: 5px;
    }
    .bussiness span{
        padding-left: 10px;
    }
    .orderInfo{
        margin-left: 10px;
    }
    .infoMes{
        margin-bottom: 50px;
    }
    .innerMsg{
        margin-top: 20px;
        margin-left: 20px;
    }
    .money {
        margin-left: 50px;
    }
    .user-info {
        display: flex;
        align-items: center;
    }

    .user-avator {
        font-size: 50px;
    }

    .user-info-cont {
        padding-left: 20px;
        flex: 1;
        font-size: 14px;
        color: #999;
    }

    .user-info-cont div:first-child {
        font-size: 30px;
        color: #222;
    }

    .user-info-list span {
        margin-left: 70px;
    }
    .orderInner{
        margin-top: 15px;
    }
</style>